<template>
    <div class="row">
        <div class="col-md-3"></div>
        <div class="col-md-6">
            <h2>  </h2>
            <br><br>
                <input type="text" v-model="newSeller.id" class="form-control" placeholder="">
                <small id="idHelp" class="form-text text-muted float-left"> 6  .</small>
                <br><br>
                <input type="password" v-model="newSeller.password" class="form-control" placeholder="">
                <small id="passwordHelp" class="form-text text-muted float-left"> 8 ,  /, ,   .</small>
                <br><br>
                <input type="text" v-model="newSeller.name" class="form-control" placeholder="">
                <br>
                <input type="text" v-model="newSeller.tel" class="form-control" placeholder="">
                <br>
                <input type="text" v-model="newSeller.addr" class="form-control" placeholder="">
                <br>
                <input type="text" v-model="newSeller.email" class="form-control" placeholder=" abc@example.com">
                <br>
                <input type="text" v-model="newSeller.crn" class="form-control" placeholder="  ">
                <small id="crnHelp" class="form-text text-muted float-left">  10 </small>
                <br><br>
                <input type="text" v-model="newSeller.company" class="form-control" placeholder="">
                <br><br>
                <button @click="newSellerSubmit" type="button" class="btn btn-primary"></button>
        </div>
        <div class="col-md-3"></div>
    </div>
</template>

<script>
    var indi = 0;
    var seller = 1;
    export default {
        name: "RegisterEnterpriseSeller",
        data(){
            return {
                newSeller : {
                    id: '',
                    password: '',
                    name: '',
                    tel: '',
                    addr: '',
                    email: '',
                    crn: '',
                    company: '',
                    indi: indi,
                    seller: seller
                }
            }
        },
        methods : {
            newSellerSubmit : function () {
                let pathuser = {
                    path : '/users/registerEnt',
                    user : this.newSeller
                };
                this.$store.dispatch('REGISTER', pathuser)
                    .then( response => {
                        if(response.data.success == true) {
                            alert('.');
                            console.log('Enterprise Seller user register Success');
                            this.$router.replace({ path : '/Login' });
                        } else {
                            alert(' .');
                            console.log('Enterprise Seller user register Failure')
                        }
                    }).catch( err => {
                    console.log('Enterprise Seller user register Err : '+ err);
                });
            }
        }
    }
</script>

