<template>
    <div class="row">
        <div class="col-md-3"></div>
        <div class="col-md-6">
            <h2>   </h2>
            <br><br>
                <input type="text" v-model="newUser.id" class="form-control" placeholder="">
                <small id="idHelp" class="form-text text-muted float-left"> 6  .</small>
                <br><br>
                <input type="password" v-model="newUser.password" class="form-control" placeholder="">
                <small id="passwordHelp" class="form-text text-muted float-left"> 8 ,  /, ,   .</small>
                <br><br>
                <input type="text" v-model="newUser.name" class="form-control" placeholder="">
                <br>
                <input type="text" v-model="newUser.tel" class="form-control" placeholder="">
                <br>
                <input type="text" v-model="newUser.addr" class="form-control" placeholder="">
                <br>
                <input type="text" v-model="newUser.email" class="form-control" placeholder=" abc@example.com">
                <br>
                <input type="number" v-model="newUser.crn" class="form-control" placeholder=" ">
                <small id="crnHelp" class="form-text text-muted float-left">  10 .</small>
                <br><br>
                <input type="text" v-model="newUser.company" class="form-control" placeholder="">
                <br><br>
                <button @click="newUserSubmit" type="button" class="btn btn-primary"></button>
        </div>
        <div class="col-md-3"></div>
    </div>
</template>

<script>
    var indi = 0;
    var seller = 0;
    export default {
        name: "RegisterEnterpriseBuyer",
        data(){
            return {
                newUser : {
                    id: '',
                    password: '',
                    name: '',
                    tel: '',
                    addr: '',
                    email: '',
                    indi: indi,
                    crn: '',
                    company: '',
                    seller: seller
                }
            }
        },
        methods : {
            newUserSubmit : function () {
                let pathuser = {
                    path : '/users/registerEnt',
                    user : this.newUser
                };
                this.$store.dispatch('REGISTER', pathuser)
                    .then( response => {
                        if(response.data.success == true) {
                            alert('.');
                            console.log('Enterprise user register Success');
                            this.$router.replace({ path : '/Login' });
                        } else {
                            alert(' .');
                            console.log('Enterprise user register Failure')
                        }
                    }).catch( err => {
                        console.log('Enterprise user register Err : '+ err);
                    });
            }
        }
    }
</script>

