<template>
    <div class="row" v-if="user.id">
        <div class="col-md-3"></div>
        <div class="col-md-6">
            <div class="row">
                <h2>  </h2>
                <br><br><br>
                <div class="card col-md-11">
                    <div class="card-body">
                        <h5 class="card-text">
                            <p>
                                <strong> </strong>   ,        .
                            </p>
                            <strong> </strong>   .
                        </h5>
                    </div>
                </div>
            </div>
            <br>
            <div class="row" v-if="tempCert.public">
                <div class="card col-md-11">
                    <div class="card-body">
                        <div v-if="(tempCert.allowed == 0) && (tempCert.disable == 1)">
                            <h4 class="card-title">
                                <strong>     </strong></h4>
                            <h5 class="card-text">
                                <strong>  : {{tempCert.deviceID}}</strong>
                            </h5>
                        </div>
                        <div v-if="(tempCert.allowed == 1) && (tempCert.disable == 0)">
                            <h4 class="card-title"><strong>    </strong></h4>
                            <br>
                            <h5 class="card-text">
                                <p>
                                    <strong>  : {{tempCert.deviceID}}</strong>
                                    <br><br>
                                    <button @click="AddCertIssue" type="button" class="btn btn-primary btn-block"></button>
                                </p>
                            </h5>
                        </div>
                    </div>
                </div>
            </div>
            <br><br>
            <div class="row">
                <h6>  </h6>
                <input type="text" v-model="deviceId" aria-describedby="DeviceHelp" class="form-control col-md-11" placeholder=" ">
                <h5 class="col-md-11"><small id="DeviceHelp" class="form-text text-muted">     .    .</small><br></h5>
                <h6>   </h6>
                <input type="password" v-model="p" class="form-control col-md-11" placeholder="">
                <br><br><br>
                <h6>   </h6>
                <input type="password" v-model="c" class="form-control col-md-11" placeholder=" ">
                <h3 v-if="m.co == true" style="color:green;" class="col-md-1"></h3>
                <h2 v-if="m.co == false" style="color:red;" class="float-left">&nbsp;</h2>
                <br><br><br><br>
                <button @click="AddCertSubmit" type="button" class="btn btn-primary col-md-11">  </button>
            </div>
            <br><br><br>
            <!--
            <h6 v-if="m.co == true" style="color:green;" class="float-left"></h6>
            <h6 v-if="m.co == false" style="color:red;" class="float-left">{{m.me}}</h6>
            <br><br>
            -->
        </div>
        <div class="col-md-3"></div>
    </div>
</template>

<script>
    export default {
        name: "AdditionalCert",
        data(){
            return {
                deviceId : '',
                p : '',
                c : '',
                m : {
                    me : '',
                    co : Boolean
                },
                user : {},
                tempCert : {},
            }
        },
        created() {
            this.$store.dispatch('GetProfile')
                .then( response => {
                    //console.log(' '+JSON.stringify(response.data.user));
                    console.log(' ');
                    this.user = response.data.user;

                    return this.$store.dispatch('AddCertAllowCheck', this.user);
                }, err => {
                    console.log(' ' + err);
                    //this.$store.dispatch('LOGOUT');
                    //this.$router.replace({path : '/Login'});
                })
                .then( response => {
                    console.log('AddCertAllowCheck response : '+JSON.stringify(response));
                    if (response.data.success == true) {
                        this.tempCert = response.data.result[0];
                    }
                })
                .catch( err => {
                    console.log(' ' + err);
                    //this.$store.dispatch('LOGOUT');
                    //this.$router.replace({path : '/Login'});
                });
        },
        methods : {
            AddCertSubmit : function () {
                if ( (this.p == this.c ) && (this.m.co == true) ) {

                    let certVal = {
                        user : this.user,
                        deviceId: this.deviceId
                    };

                    this.$store.dispatch('AddCert04', certVal)
                        .then( response => {
                            console.log('AddCert04 : '+JSON.stringify(response));
                            if (response.data.success == true) {
                                let certR = {
                                    pa : this.p,
                                    user : this.user,
                                    deviceId: this.deviceId
                                };
                                return this.$store.dispatch('AddCertRequest', certR);
                            } else {
                                console.log('AddCert04 Validate false');
                                alert('     .');
                                this.deviceId = '';
                                this.p = '';
                                this.c = '';
                            }
                        })
                        .then( response => {
                            if(response.data.success == true) {
                                console.log('Cert Request 1 : '+JSON.stringify(response));
                                var re = {
                                    response : response.data,
                                    id : this.user.id,
                                    deviceId : this.deviceId
                                };
                                this.$store.dispatch('storeACert', re);
                                alert('Cert Request Success');
                                console.log('Cert Request Success');
                                this.$router.replace({path : '/CertificationCenter'});
                            } else {
                                this.$store.dispatch('deletePem', certVal);
                                console.log('Cert Request Failure');
                                alert('Cert Request Failure');
                            }
                        }).catch( err => {
                        console.log('Cert Request Err : '+ err);
                    });
                }
                else {
                    alert('  ');
                }
            },
            AddCertIssue : function () {
                var issue = {
                    cert : this.tempCert.cert,
                    user : this.user
                };
                this.$store.dispatch('storeACertissue', issue);
                alert('  .');
                this.$router.replace({path : '/CertificationCenter'});
            }
        },
        watch : {
            p : function (pa) {
                if ( this.c == '' ) {

                }
                else if ( pa == this.c ) {
                    this.m.co = true;
                    this.m.me = '';
                } else {
                    this.m.co = false;
                    this.m.me = ' .';
                }
            },
            c : function (confirm) {
                if ( confirm == this.p ) {
                    this.m.co = true;
                    this.m.me = '';
                } else {
                    this.m.co = false;
                    this.m.me = ' .';
                }
            }
        }
    }
</script>

<style scoped>

</style>
